@import url('https://fonts.googleapis.com/css?family=Caveat:700');
@import url('https://fonts.googleapis.com/css?family=Rubik:400,400i,700,700i&display=swap&subset=latin-ext');
@import url('https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap&subset=latin-ext');
@import '_normalize';
@import '_variables';

@import 'modules/navigation.scss';
@import 'modules/homepage.scss';
@import 'modules/gallery.scss';
@import 'modules/about.scss';

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  color: $dark;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

.footer {
  padding: 100px 0 50px;

  &__path svg {
    max-width: 80%;
  }

  &__paragraph {
    font-size: 10px;
    letter-spacing: 1px;
    margin-top: 180px;
  }
}

.header {
  bottom: 5%;
  font-family: 'Caveat', sans-serif;
  font-size: 36px;
  line-height: 1.2em;
  max-width: 180px;
  position: fixed;
  right: 10%;

  @media (max-width: $mobile) {
    max-width: 60%;
    position: static;
  }

  &__link {
    text-decoration: none;
    color: $apricot;
  }
}

.instagram {
  padding: 75px 10%;

  &__header {
    font-size: 28px;
    font-weight: bold;
    text-transform: uppercase;
  }

  &__gallery {
    display: flex;
    @media (max-width: $mobile) {
      flex-wrap: wrap;
    }
  }

  &__link {
    @media (max-width: $mobile) {
      width: 50%;
    }
  }

  &__image {
    display: block;
    width: 100%;
  }
}
