.homepage {
  display: flex;
  flex-wrap: wrap;

  &__header {
    width: 100%;
  }

  &__title {
    display: inline-block;
    font-size: 28px;
    font-weight: bold;
    margin: 0 0 75px;
    text-transform: uppercase;
    width: 500px;

    @media (max-width: $mobile) {
      width: 90%;
    }
  }

  &__photo {
    margin: 0 0 75px;

    &--main-image {
      height: 100vh;
      width: 100%;
    }

    &--1 {
      //   width: 90%;
      //   margin-left: 5%;
    }

    &--2 {
      width: 50%;

      @media (max-width: 450px) {
        width: 100%;
      }
    }

    &--small {
      padding: 15% 10%;
      width: 50%;
    }
  }

  &__image {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
}
